import * as Types from './mutation-typs'

export default {
  [Types.TOGGLE_POPUP_FILTER] (state, isShown) {
    state.shownPopupFilter = isShown
  },
  [Types.SEGMENT_LIST_REQUEST] (state) {
    state.segmentList.requesting = true
    state.segmentList.resData = []
  },
  [Types.SEGMENT_LIST_SUCCESS] (state, list) {
    state.segmentList.requesting = false
    state.segmentList.resData = list
  },
  [Types.SEGMENT_LIST_FAILURE] (state) {
    state.segmentList.requesting = false
  },
  [Types.TASK_LIST_OF_SEGMENT_REQUEST] (state) {
    state.taskListOfSegment.requesting = true
    state.taskListOfSegment.resData = []
  },
  [Types.TASK_LIST_OF_SEGMENT_SUCCESS] (state, list) {
    state.taskListOfSegment.requesting = false
    state.taskListOfSegment.resData = list
  },
  [Types.TASK_LIST_OF_SEGMENT_FAILURE] (state) {
    state.taskListOfSegment.requesting = false
  }
}



