import Vue from 'vue'
import vuexI18n from 'vuex-i18n'
import LocalePlugin from 'vux/src/plugins/locale/index.js'

import objectAssign from 'object-assign'
import vuxLocales from 'vux/src/locales/all.yml'
import store from './store'
import globalLocales from './locales/global.yml'
import componentsLocales from './locales/components.yml'

Vue.use(vuexI18n.plugin, store)

initLocales()

Vue.use(LocalePlugin)

const nowLocale = Vue.locale.get()
if (/zh/.test(nowLocale)) {
Vue.i18n.set('zh-CN')
} else {
Vue.i18n.set('en')
}

function initLocales () {
const localeList = ['en', 'zh-CN']
localeList.forEach(locale => {
const localeObj = objectAssign(vuxLocales[locale], globalLocales[locale], componentsLocales[locale])
Vue.i18n.add(locale, localeObj)
})
}



