import api from '@/api'
import * as rootTypes from '@/store/mutation-typs'
import * as Types from './mutation-typs'

export default {
  // 筛选 popup 显隐开关
  togglePopupFilter ({ commit }, isShown) {
    commit(Types.TOGGLE_POPUP_FILTER, !!isShown)
  },
  // 获取流水段列表
  async fetchSegmentList ({ commit, rootState }, data) {
    const { userId } = rootState.appContextInfo
    const _data = {
      itemTypes: '',
      participantIds: userId,
      periodId: '',
      sortType: 'asc',
      responsibleIds: userId,
      sortColumn: 'itemType',
      excludeCreatorId: 1,
      segmentIds: '',
      delayStatus: '',
      itemStatus: '0',
      ...data
    }
    commit(Types.SEGMENT_LIST_REQUEST)
    commit(rootTypes.RESET_ERROR_MSG, null, { root: true })
    const { projectId } = rootState
    const res = await api.taskTrace.loadSegmentList(projectId, _data)
      .catch(() => {
        commit(Types.SEGMENT_LIST_FAILURE)
      })

    if (res.code === 0) {
      commit(Types.SEGMENT_LIST_SUCCESS, res.data.list || [])
    } else {
      commit(rootTypes.SET_ERROR_MSG, res.message, { root: true })
      commit(Types.SEGMENT_LIST_FAILURE)
    }
  },
  // 获取某个流水段的任务列表
  async fetchTaskListOfSegment ({ commit, rootState }, data) {
    commit(Types.TASK_LIST_OF_SEGMENT_REQUEST)
    commit(rootTypes.RESET_ERROR_MSG, null, { root: true })
    const { projectId } = rootState
    const res = await api.taskTrace.loadTaskListOfSegment(projectId, data)
      .catch(() => {
        commit(Types.TASK_LIST_OF_SEGMENT_FAILURE)
      })

    if (res.code === 0) {
      commit(Types.TASK_LIST_OF_SEGMENT_SUCCESS, res.data.list || [])
    } else {
      commit(rootTypes.SET_ERROR_MSG, res.message, { root: true })
      commit(Types.TASK_LIST_OF_SEGMENT_FAILURE)
    }
  }
}



