import api from '../api'
import * as Types from './mutation-typs'

export default {
  setErrorMsg ({ commit }, errorMsg) {
    commit(Types.SET_ERROR_MSG, errorMsg)
  },
  resetErrorMsg ({ commit }) {
    commit(Types.RESET_ERROR_MSG)
  },
  // 获取当前用户信息
  async fetchUserInfo ({ commit }) {
    commit(Types.USER_INFO_REQUEST)
    commit(Types.RESET_ERROR_MSG)
    const res = await api.loadUserInfo()
      .catch(() => {
        commit(Types.USER_INFO_FAILURE)
      })

    if (res instanceof Object) {
      commit(Types.USER_INFO_SUCCESS, res)
    } else {
      const errorMsg = (res && res.message) || '用户信息加载失败！'
      commit(Types.USER_INFO_FAILURE)
      commit(Types.SET_ERROR_MSG, errorMsg)
    }
  },
  // 获取当前项目信息
  async fetchProjectInfo ({ commit, state }) {
    commit(Types.RESET_ERROR_MSG)
    commit(Types.PROJECT_INFO_REQUEST)
    const res = await api.getProjectInfo(state.projectId)
      .catch(() => {
        commit(Types.PROJECT_INFO_FAILURE)
      })

    if (res.code === 0) {
      commit(Types.PROJECT_INFO_SUCCESS, res.data)
    } else {
      commit(Types.PROJECT_INFO_FAILURE)
      commit(Types.SET_ERROR_MSG, res.message)
    }
  },
  // 设置 App context 信息
  setAppContextInfo ({ commit }, data) {
    commit(Types.SET_APP_CONTEXT_INFO, data)
    commit(Types.SET_TOKEN, data.gcloudToken)
  },
  // 设置 Token
  setToken ({ commit }, token) {
    commit(Types.SET_TOKEN, token)
  },
  // 更新当前项目
  updateCurrentProject ({ commit }, project) {
    commit(Types.UPDATE_CURRENT_PROJECT, project)
  }
}



