import axios from 'axios'
const baseUrl = '/api/project-service/v4'

// 项目相关 API
export default {
  // 获取项目信息
  getProjectInfo (projectId) {
    return axios.get(`${baseUrl}/projects/${projectId}/info`)
      .then(res => res.data)
  },
  // 获取项目效果图列表
  getProjectImgList (projectId) {
    return axios.get(`${baseUrl}/projects/${projectId}/effectPictures/list`)
      .then(res => res.data)
  }
}



