import Vue from 'vue'
import axios from 'axios'
import store from '../store'

Vue.prototype.$axios = axios

axios.interceptors.request.use(
  config => {
    // cloudT token
    const cloudToken = store.state.token
    config.headers.common['Authorization'] = `Bearer ${cloudToken}`

    // 声明这是ajax请求,便于后端返回权限校验码;
    config.headers.endpoint = `AJAX`

    return config
  },
  error => Promise.reject(error)
)

axios.interceptors.response.use(
  response => {
    return response
  },
  error => {
    const errMessage = (error.response && error.response.data && error.response.data.message) || error.message
    if (error.response.status === 401) {
      window.bim5dApp.gotoLogout()
    } else if (errMessage) {
      store.actions.resetErrorMsg()
      store.actions.setErrorMsg(errMessage)
    }
    return Promise.reject(error)
  }
)

export default axios



