import * as Types from './mutation-typs'

export default {
  [Types.SET_ERROR_MSG] (state, payload) {
    state.errorMsg = payload
  },
  [Types.RESET_ERROR_MSG] (state) {
    state.errorMsg = null
  },
  [Types.SET_APP_CONTEXT_INFO] (state, data) {
    state.appContextInfo = data
  },
  [Types.SET_TOKEN] (state, token) {
    state.token = token
  },
  [Types.USER_INFO_REQUEST] (state) {
    state.requesting = true
    state.userInfo = {}
  },
  [Types.USER_INFO_SUCCESS] (state, payload) {
    state.userInfo = payload
    state.requesting = false
  },
  [Types.USER_INFO_FAILURE] (state) {
    state.requesting = false
  },
  [Types.PROJECT_INFO_REQUEST] (state) {
    state.requesting = true
    state.projectInfo = {}
  },
  [Types.PROJECT_INFO_SUCCESS] (state, payload) {
    state.requesting = false
    state.projectInfo = payload
  },
  [Types.PROJECT_INFO_FAILURE] (state) {
    state.requesting = false
  },
  [Types.UPDATE_CURRENT_PROJECT] (state, project) {
    const { id } = project
    state.projectId = id
    localStorage.setItem('projectId', id)
  }
}



