import axios from 'axios'
import project from './project' // 项目信息相关 API
import taskTrace from './taskTrace' // 任务跟踪 API

const baseUrl = '/cloudt'

export default {
  // 获取组织树信息
  loadOrgTree (params) {
    return axios.get(`${baseUrl}/org/navigate-tree`, { params })
      .then(res => res.data)
  },
  // 获取用户信息
  loadUserInfo () {
    return axios.get(`${baseUrl}/session/user`)
      .then(res => res.data)
  },
  ...project,
  taskTrace
}



