
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { mapState, mapActions } from 'vuex'
export default {
name: 'Projects',
components: {
Checker,
CheckerItem,
Flexbox,
FlexboxItem
},
computed: {
...mapState(['projectId'])
},
data () {
return {
projectData: [
{name: '武汉某研发楼大厦', time: '2018-12-12 17:32:16', id: '123', pic: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546581812474&di=373bf03bf8b04de0ad35c7904ad69482&imgtype=0&src=http%3A%2F%2Fe.hiphotos.baidu.com%2Fimage%2Fpic%2Fitem%2F3b292df5e0fe9925a8a324c539a85edf8cb171f3.jpg'},
{name: '武汉某研发楼大厦', time: '2018-12-12 17:32:16', id: '123', pic: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546581812474&di=373bf03bf8b04de0ad35c7904ad69482&imgtype=0&src=http%3A%2F%2Fe.hiphotos.baidu.com%2Fimage%2Fpic%2Fitem%2F3b292df5e0fe9925a8a324c539a85edf8cb171f3.jpg'},
{name: '武汉某研发楼大厦', time: '2018-12-12 17:32:16', id: '123', pic: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546581812474&di=373bf03bf8b04de0ad35c7904ad69482&imgtype=0&src=http%3A%2F%2Fe.hiphotos.baidu.com%2Fimage%2Fpic%2Fitem%2F3b292df5e0fe9925a8a324c539a85edf8cb171f3.jpg'},
{name: '武汉某研发楼大厦', time: '2018-12-12 17:32:16', id: '123', pic: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1546581812474&di=373bf03bf8b04de0ad35c7904ad69482&imgtype=0&src=http%3A%2F%2Fe.hiphotos.baidu.com%2Fimage%2Fpic%2Fitem%2F3b292df5e0fe9925a8a324c539a85edf8cb171f3.jpg'}
]
}
},
methods: {
...mapActions(['updateCurrentProject']),
async loadOrgTree () {
const params = {
product_code: 'bim5d',
projected: true,
bureau: true
}
let res = await this.$api.loadOrgTree(params)
let tempArray = [...[res]]
if (res.code === 0) {
this.projectData = this.$utils.filterProjectTree(tempArray)
} else {
this.$vux.alert.show({
title: '错误信息',
content: res.message
})
}
},
switchProject (record) {
this.updateCurrentProject(record)
this.$router.push('/')
}
},
mounted () {

}
}



