
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import numberPad from 'vux/src/tools/number/pad.js'
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import { mapState, mapActions } from 'vuex'
import Sub from '@/components/addlabor/Sub.vue'
import { labor } from '@/components/addlabor/Labor.js'
import moment from 'moment'
import { getIntFromStr } from '@/components/addlabor/util.js'

let dateFrm = 'YYYY-MM-DD HH:mm'
let rawtoken = 'cn-3a25c24f-5c1e-447a-a045-dc9133c07eb3'
let token = 'Bearer ' + 'cn-3a25c24f-5c1e-447a-a045-dc9133c07eb3'
let headers = {
CLIENTVERSION: 'V1',
Authorization: token
}
export default {
name: 'AddLabor',
components: {
Checker,
CheckerItem,
Flexbox,
FlexboxItem,
Datetime,
Group,
Sub,
Grid,
GridItem
},
computed: {
...mapState(['projectId']),
occurredDate () {
if (labor.occurredDate === 0) return ''
return moment(labor.occurredDate).format(dateFrm)
}
},
data () {
return {
labor,
labortime: '2018-09-09 12:23',
minutelist: [],
subcontractorMap: new Map(),
subcontractorWorkTypeMap: new Map(),
workTypeMap: new Map(),
subs: [],
projectId: '1572451974416296',
dataUrl: ''
}
},
methods: {
...mapActions(['updateCurrentProject']),
submit () {
console.log('on click')
},
timechange (d) {
this.labor.occurredDate = moment(d, dateFrm)
},
typechanged (statisticsType) {
if (this.labor.statisticsType !== statisticsType) {
if (statisticsType === 'nose_count') {
for (let i = 0; i < this.labor.workTypes.length; i++) {
let wt = this.labor.workTypes[i]
wt.count = getIntFromStr(wt.workDayCount)
}
} else {
for (let i = 0; i < this.labor.workTypes.length; i++) {
let wt = this.labor.workTypes[i]
wt.workDayCount = `${wt.count}`
}
}
this.labor.statisticsType = statisticsType
this.assemblesubs()
}
},
submit2 () {
let url = 'api/v1/projects/15633293392477074065/labor/save'
let s =
'{"attachments":[{"file":"dfac01bf70c44fdaba6ff633f2d237e1","size":321478,"name":"1584054770627586_20190108_142132_635.jpg","fileHash":"ce3e8dd347fd4c7eba606d4865125bf7","id":"1584054771024899","type":"image","createDate":0}],"photoTypeId":"1577954065444269","remark":"wx2新建新建","laborId":"","statisticsType":"nose_count","workTypes":[{"count":5166331,"workTypeRelationId":"1577315124704072"}],"itemId":"1578489421998509","itemName":"土方","createdDate":"1546928228863","createdBy":"6298842148508439286","segmentId":"6017570233752594463","startTime":0,"endTime":0,"occurredDate":"1546928228866"}'
let json = JSON.parse(s)
this.$axios
.post(url, json, { headers })
.then(r => console.log(r))
.catch(e => console.log(e))
},
assemblesubs () {
console.log('----------')
let size = this.labor.workTypes.length
let outlist = []
for (let i = 0; i < size; i++) {
let wt = this.labor.workTypes[i]
let relation = this.subcontractorWorkTypeMap.get(wt.workTypeRelationId)
if (!relation) return
if (!JSON.stringify(outlist).includes(wt.workTypeRelationId)) {
let name = this.subcontractorMap.get(relation.subcontractorId).name
let sub = {id: wt.workTypeRelationId, name}
sub.list = []
outlist.push(sub)
}
let sub = outlist[outlist.length - 1]
let name = this.workTypeMap.get(relation.workTypeId).name
wt.name = name
sub.list.push(wt)
}
this.subs = outlist
},
addphoto () {
console.log('addphoto')
let that = this
window.cloudtplus.getOfflinePicture({
success: function (data) {
alert(JSON.stringify(data))
window.cloudtplus.getDataUrl({
success: function (dataUrl) {
alert(dataUrl)
that.dataUrl = dataUrl

},
error: function (e) {
alert(e)
},
url: data[0].fileUrl + '-thumb.png',
force: true
})
},
error: function (e) {
alert(e)
},
orgId: '',
projectId: this.projectId,
type: '',
tag1: '',
tag2: '',
tag3: '',
tag4: '',
tag5: '',
sourceType: 'CAMERA',
maxCount: 1,
cropRate: 1,
allowVideo: true,
watermark: '2018-06-12',
continuous: false 
})















},

upLoadFn (data) {
const fileName = data[0].fileId
const suffix = fileName.split('.')[1]
const params = {
projectId: this.projectId,
businessType: 'Quality',
suffix: suffix
}
let url = `/api/project-service/v4/projects/${this.projectId}/uploadUrl`

this.$axios.get(url, params, {headers})
.then(r => {
const uploadUrl = r.data
window.cloudtplus.updateOfflineFile({
fileId: fileName,
gcloudUploadUrl: uploadUrl,
success () {
window.cloudtplus.uploadOfflineFile({
fileId: fileName,
success (file) {
console.log('file:', file)


},
error (e) {
console.log('in', e)
}
})
},
error (e) {
console.log('out', e)
}
})
})
.catch(e => console.log(e))
},
urlstr (fileId) {
let s = 'http://bim5d-pro-test.glodon.com/api/v1/projects/1572451974416296/fileUpload/toDownloadUrl/fileId/' + fileId + '?access_token=' + rawtoken
console.log(s)
return s
}
},
mounted () {
for (let i = 0; i < 60; i++) {
this.minutelist.push('' + numberPad(i))
}

let bstr = '{"subcontractorWorkType":[],"workType":[],"subcontractor":[]}'
let b = JSON.parse(bstr)
let url = `api/v1/projects/${this.projectId}/webapi/common_method/single_table/alldetails`
this.$axios
.post(url, b, { headers })
.then(r => {
let subcontractors = r.data.data.subcontractor || []
for (let i = 0; i < subcontractors.length; i++) {
let sub = subcontractors[i]
this.subcontractorMap.set(sub.id, sub)
}

let subcontractorWorkTypes = r.data.data.subcontractorWorkType || []
for (let i = 0; i < subcontractorWorkTypes.length; i++) {
let swt = subcontractorWorkTypes[i]
this.subcontractorWorkTypeMap.set(swt.id, swt)
}

let workTypes = r.data.data.workType || []
for (let i = 0; i < workTypes.length; i++) {
let wt = workTypes[i]
this.workTypeMap.set(wt.id, wt)
}

this.assemblesubs()
})
.catch(e => console.log(e))
}
}



