import axios from 'axios'
const baseUrl = '/api/v1'

export default {
  // 任务跟踪主页——流水段列表
  loadSegmentList (projectId, data) {
    return axios.post(`${baseUrl}/projects/${projectId}/weeklyPlan/item/segment/list`, data)
      .then(res => res.data)
  },
  // 任务跟踪主页——单个流水段的任务列表
  loadTaskListOfSegment (projectId, data) {
    return axios.post(`${baseUrl}/projects/${projectId}/weeklyPlan/item/list`, data)
      .then(res => res.data)
  }
}



