import { mapState, mapActions } from 'vuex'

export default {
  name: 'App',
  computed: {
    ...mapState(['errorMsg'])
  },
  watch: {
    errorMsg (newVal) {
      newVal && this.$vux.toast.show({
        text: newVal,
        type: 'warn'
      })
    }
  },
  created () {
    const that = this

    // 判断是否有codova对象
    if (window.cloudtplus && window.cloudtplus.onDeviceReady) {
      window.cloudtplus.onDeviceReady(function () {
        window.cloudtplus.getContextInfo({
          success (data) {
            if (data) {
              that.setAppContextInfo(data)
            }
          },
          error (err) {
            that.setErrorMsg(err)
          }
        })
      })
    }
  },
  methods: {
    ...mapActions(['setAppContextInfo', 'setErrorMsg'])
  }
}



