import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import mutations from './mutations'
import taskTrace from './modules/taskTrace'

Vue.use(Vuex)

let appContextInfo = {
  gcloudToken: '',
  userId: ''
}
if (process.env.NODE_ENV === 'development') {
  appContextInfo = {
    gcloudToken: 'Cn-3a25c24f-5c1e-447a-a045-dc9133c07eb3',
    userId: '6298842148508439286'
  }
}

const projectId = localStorage.getItem('projectId')

export default new Vuex.Store({
  state: {
    token: appContextInfo.gcloudToken,
    projectId,
    errorMsg: null,
    successMsg: null,
    requesting: false,
    userInfo: {},
    appContextInfo,
    projectInfo: {}
  },
  mutations,
  actions,
  modules: {
    taskTrace
  }
})



