const upperFirst = (str) => {
  return str.substring(0, 1).toUpperCase() + str.substring(1)
}

const filterProjectTree = (data) => {
  let projects = []
  for (var i in data) {
    for (var j in data[i]) {
      if (j === 'childNodes') {
        if (data[i][j]) {
          data[i][j].forEach((item) => {
            if (item.project) {
              projects = [...projects, ...[item]]
            }
            if (item.childNodes) {
              filterProjectTree(item.childNodes)
            }
          })
        }
      }
    }
  }
  return projects
}
export {
  upperFirst,
  filterProjectTree
}



